import App from '../../App.vue'
import uniCard from "@/components/uni-card/uni-card.vue"
import uniSwipeAction from "@/components/uni-swipe-action/uni-swipe-action.vue"

export default {
	components: {
		uniCard,
		uniSwipeAction
	},
	data() {
		return {
			options: [{
				text: '置顶',
				style: {
					backgroundColor: '#707070'
				}
			}, {
				text: '删除',
				style: {
					backgroundColor: '#dd524d'
				}
			}],
			top: 0,
			//演示文章数据
			artList: [],
			currentPage: 0,
			count: -1,
			skip: 0,
			limit: 7
		};
	},
	onLoad: function(options) {
		setTimeout(function() {
			console.log('start pulldown');
		}, 1000);
		uni.startPullDownRefresh();
	},
	//下拉刷新
	onPullDownRefresh: function() {
		// 重置分页及数据
		this.skip = 0,
			this.limit = 7, this.currentPage = 0, this.count = -1;
		this.artList = [];
		this.getNewsList();
	},
	// 加载更多
	onReachBottom: function() {
		this.skip = this.limit + this.skip;
		this.getNewsList();
	},
	onNavigationBarButtonTap(value) {
		if (value.float == "right") {
			uni.navigateTo({
				url: '/pages/index/newNote'
			})
		}else{
			console.log("caidan")
		}
	},
	methods: {
		mdDetail(note) {
			uni.navigateTo({
				url: '/pages/index/mdDetail',
			})
		},
		// 数据和分页是模拟的，实际也是这样写
		getNewsList: function() {
			let info = uni.getStorageSync("detail")
			uni.showLoading({});
			uni.request({
				// url: 'https://www.easy-mock.com/mock/5cb9655c01e2e57715d324b0/example/imgnewlist?page='+page+'#!method=get&cate='+cate,
				url: App.requestIp + "note/selectNoteByUserAccount/" + info.account + '/' +
					this.skip + '/' + this.limit,
				method: "GET",
				header: {
					token: uni.getStorageSync('token')
				},
				data: {},
				success: res => {
					console.log(res.data);
					var newsList = res.data.result;
					this.count = res.data.total / this.limit
					console.log(this.count)
					this.currentPage++;
					if (this.count >= 0 && this.currentPage < this.count) {
						this.artList = this.artList.concat(newsList);
					}
					uni.hideLoading();
				},
				complete: res => {
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		bindClick(value) {
			console.log(value.text)
			uni.showToast({
				title: `点击了${value.text}按钮`,
				icon: 'none'
			})

		},
	}

}
